<template>
	<div id="hauler-results">
		<section class="panel panel-default">
			<header class="panel-heading">
				<strong>Searched Address:</strong>
				{{ hauler.Address }}
			</header>
			<div class="table-responsive">
				<table class="table table-striped table-bordered">
					<tbody>
						<tr>
							<td>
								<strong>Trash Days</strong><br>
								<span class="small">
									Next Pickups
								</span>
							</td>
							<td>
								<strong>
									{{ garbageDays }}
								</strong>
								<ul class="small list-unstyled">
									<li v-for="date in nextGarbageDays">
										{{ date }}
									</li>
								</ul>
							</td>
						</tr>
						<tr>
							<td>
								<strong>Recycling Day</strong><br>
								<span class="small">
									Next Pickup
								</span>
							</td>
							<td>
								<strong>
									{{ recycleDays }}
								</strong>
								<ul class="small list-unstyled">
									<li v-for="date in nextRecycleDays">
										{{ date }}
									</li>
								</ul>
							</td>
						</tr>
						<tr>
							<td>
								<strong>Yard Waste Day</strong><br>
								<span class="small">
									Next Pickup
								</span>
							</td>
							<td>
								<strong>
									{{ yardWasteDays }}
								</strong>
								<ul class="small list-unstyled">
									<li v-for="date in nextYardWasteDays">
										{{ date }}
									</li>
								</ul>
							</td>
						</tr>
						<tr>
							<th>Provider</th>
							<td v-if="provider">
								<strong>{{ provider.name }}</strong><br>
								<span v-html="provider.address"></span><br>
								<i class="fa fa-fw fa-phone"></i> <span class="sr-only">Phone:</span> {{ provider.phone }}<br>
								<i class="fa fa-fw fa-fax"></i> <span class="sr-only">Fax:</span> {{ provider.fax }}<br>
								<i class="fa fa-fw fa-envelope"></i> <span class="sr-only">Email:</span>
								<a :href="'mailto:'+provider.email" v-text="provider.email"></a>
							</td>
						</tr>
					</tbody>
				</table>
			</div>
		</section>

	</div>
</template>

<script>
import * as esriLoader from 'esri-loader'
import _ from 'underscore'

export default {
	name: 'hauler-results',
	data () {
		return {
			msg: 'Hauler Results',
			hauler: {
				Address: '',
				Garbage: '',
				Recycling: '',
				YardWaste: '',
				Provider: ''
			}
		}
	},
	watch: {
		'$route': function() {
			this.fetchHaulerInfo()
		}
	},
	computed: {
		folio: function() {
			return this.$route.params.folio
		},
		garbageDays: function() {
			return this.getDaysOfWeek(this.hauler.Garbage)
		},
		nextGarbageDays: function() {
			return this.nextPickupDays(this.hauler.Garbage)
		},
		recycleDays: function() {
			return this.getDaysOfWeek(this.hauler.Recycling)
		},
		nextRecycleDays: function() {
			return this.nextPickupDays(this.hauler.Recycling, true)
		},
		yardWasteDays: function() {
			return this.getDaysOfWeek(this.hauler.YardWaste)
		},
		nextYardWasteDays: function() {
			return this.nextPickupDays(this.hauler.YardWaste)
		},
		provider: function() {
			var providerArr = [
				{
					ids: ['WASTE MANAGEMENT', 'WASTE MANAGEMENT OF TAMPA'],
					name: 'Waste Management of Tampa',
					address: '3411 N 40th St,<br> Tampa, Fl 33605',
					phone: '(813) 621-3053',
					fax: '(813) 740-8210',
					email: 'CentralFloridaService@wm.com'
				},
				{
					ids: ['REPUBLIC WASTE'],
					name: 'Republic Services',
					address: '5210 W Linebaugh Ave,<br> Tampa, Fl 33624',
					phone: '(813) 265-0292',
					fax: '(813) 961-3534',
					email: 'republicservicesCSR@repsrv.com'
				},
				{
					ids: ['WASTE CONNECTIONS', 'PROGRESSIVE WASTE SOLUTIONS'],
					name: 'Waste Connections, Inc.',
					address: '5135 Madison Ave,<br> Tampa, Fl 33619',
					phone: '(813) 248-3802',
					fax: '(813) 248-3606',
					email: 'cs-tampa@progressivewaste.com'
				}
			]

			return _.chain(providerArr).filter( (p) => {
				return _.contains(p.ids, this.hauler.Provider)
			}).first().value()
		}
	},
	methods: {
		nextPickupDays () {
			var search_str = arguments[0]
			var is_recycling = arguments[1]

			var milidays = _.map(this.strToDowIntsArr(search_str), (dow) => {
				return this.nextXday(dow)
			}).sort()

			return _.map(milidays, (d) => {
				var date = new Date(d)
				var options = { weekday: 'long', year: 'numeric', month: 'long', day: 'numeric' }
				var date_str = date.toLocaleDateString('en-US', options)
				if (this.isHoliday(date, is_recycling)) { date_str = "HOLIDAY - NO PICKUP: " + date_str }
				return date_str
			})
		},
		getDaysOfWeek (str) {
			var milidays = _.map(this.strToDowIntsArr(str), (dow) => {
				return this.nextXday(dow)
			})
			var dows = _.map(milidays, (d) => {
				var date = new Date(d)
				var options = { weekday: 'long' }
				var date_str = date.toLocaleDateString('en-US', options)
				return date_str.split(',')[0]+'s'
			})
			return dows.join(' & ')
		},
		strToDowIntsArr (search_str) {
			var dows = [
				{str: 'sun', dow: 0},
				{str: 'mon', dow: 1},
				{str: 'tue', dow: 2},
				{str: 'wed', dow: 3},
				{str: 'thu', dow: 4},
				{str: 'fri', dow: 5},
				{str: 'sat', dow: 6}
			]
			return _.chain(dows).map((dow_obj) => {
				return (search_str.toLowerCase().indexOf(dow_obj.str) >= 0) ? dow_obj.dow : false
			}).compact().value()
		},
		nextXday (dow) {
			var d = new Date()
			// var d = new Date('5/24/2017')
			var d = new Date('6/28/2017')
			return d.setDate(d.getDate() + (dow + 7 - d.getDay()) % 7)
		},
		isHoliday () {
			var test_date = arguments[0]
			var is_recycling = arguments[1] || false
			var temp_date, date_d, date_m, date_w, date_wnum, date_str1, date_str2, date_str3

			// new years, independence, christmas
			date_d = test_date.getUTCDate()
			date_m = test_date.getUTCMonth() + 1
			date_str1 = [date_m, date_d].join('/')
			if (date_str1 === '1/1' && is_recycling) {
				return false
			}
			if (date_str1 === '1/1' || date_str1 === '7/4' || date_str1 === '12/25') {
				return true
			}

			// labor, thanksgiving
			date_w = test_date.getUTCDay()
			date_wnum = Math.floor((date_d - 1) / 7) + 1
			date_str2 = [date_m, date_wnum, date_w].join('/')
			if (date_str2 === '9/1/1' || date_str2 === '11/4/4') {
				return true
			}

			// memorial
			temp_date = new Date(test_date)
			temp_date.setDate(1)
			temp_date.setMonth(temp_date.getMonth() + 1)
			temp_date.setDate(temp_date.getDate() - 1)
			date_wnum = Math.floor((temp_date.getDate() - date_d - 1) / 7) + 1
			date_str3 = [date_m, date_wnum, date_w].join('/')
			if (date_str3 === '5/1/1') {
				return true
			}

			return false
		},
		fetchHaulerInfo () {
			esriLoader.dojoRequire([
				"esri/tasks/QueryTask",
				"esri/tasks/support/Query"
			], (QueryTask, Query) => {
				var queryTask = new QueryTask({
					url: "https://maps.hillsboroughcounty.org/arcgis/rest/services/InfoLayers/SW_HAULER_DATA2/MapServer/1"
				})
				var query = new Query()
				query.where = 'Folio=' + this.folio
				query.outFields = ['*']

				queryTask.execute(query).then( (response) => {
					if (response.features.length) {
						this.hauler = response.features[0].attributes
					} else {
						throw 'No Hauler information could be found.'
					}
				}).otherwise( (err) => {
					console.error(err)
				})
			})
		},
	},
	mounted () {
		this.fetchHaulerInfo()
	}
}
</script>

<style scoped>

</style>



