import Vue from 'vue'
import Router from 'vue-router'
// import Hello from '@/components/Hello'
import HaulerResults from '@/components/results/Hauler'

Vue.use(Router)

export default new Router({
	routes: [
		{
			path: '/',
			name: 'App',
			// component: AddressForm
		},
		{
			path: '/hauler/:folio',
			name: 'HaulerResults',
			component: HaulerResults
		}
	]
})



