<template>
	<div id="address-form">

		<form @submit.prevent="findAddress()">
			<div class="form-group input-group">
				<input class="form-control" v-model="inputAddress" :placeholder="inputAddressPlaceholder" autocomplete="off" required>
				<span class="input-group-btn">
					<button class="btn btn-info" type="submit">
						<span class="glyphicon glyphicon-search" aria-hidden="true"></span>
						Find
					</button>
				</span>
			</div>
		</form>

	</div>
</template>

<script>
import * as esriLoader from 'esri-loader'

export default {
	name: 'address-form',
	data () {
		return {
			inputAddress: '',
			inputAddressPlaceholder: 'Your Address...',
			responseAddress: null,
			responseLocation: null,
			responseParcelInfo: null
		}
	},
	watch: {
		'responseLocation': function() {
			this.findParcelInfo()
		},
		'responseParcelInfo': function() {
			this.$emit('pacelFound', this.responseParcelInfo)
		},
	},
	methods: {
		findAddress () {
			esriLoader.dojoRequire([
				"esri/tasks/Locator"
			], (Locator) => {
				var hcLocator = new Locator({
					url: "https://maps.hillsboroughcounty.org/arcgis/rest/services/Geocoding/DBO_composite_address_locator/GeocodeServer"
				});
				hcLocator.addressToLocations({
					address: { SingleLine: this.inputAddress },
					maxLocations: 1
				}).then( (response) => {
					if (response.length) {
						this.responseAddress = response[0].address
						this.responseLocation = response[0].location
					} else {
						throw 'The address could not be found.'
					}
				}).otherwise( (err) => {
					console.error(err)
				});
			})
		},
		findParcelInfo () {
			esriLoader.dojoRequire([
				"esri/tasks/QueryTask",
				"esri/tasks/support/Query"
			], (QueryTask, Query) => {
				var parcelQueryTask = new QueryTask({
					url: "https://maps.hillsboroughcounty.org/arcgis/rest/services/InfoLayers/HC_Parcels/MapServer/0"
				})
				var query = new Query()
				query.geometry = this.responseLocation
				query.outFields = ['*']

				parcelQueryTask.execute(query).then( (response) => {
					if (response.features.length) {
						this.responseParcelInfo = response.features[0].attributes
					} else {
						throw 'No Parcel information could be found.'
					}
				}).otherwise( (err) => {
					console.error(err)
				})
			})
		}
	}
}
</script>

<style scoped>
</style>



